.input-checkbox {
  width: 100%;
  border: 0;
  margin: 0;
  padding: 0;
  margin-bottom: 34px;
}

.input-checkbox__legend {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  padding: 0;
  margin-bottom: 20px;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: @tablet-width) {
    margin-bottom: 28px;
  }
}

.input-checkbox__list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 84px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 42px;
  }
}

.input-checkbox__item {
  @media (min-width: @tablet-width) {
    width: 224px;
  }

  @media (min-width: @desktop-width) {
    width: auto;
  }
}
